import React, {useEffect, useState} from 'react';
import ReactPlayer from 'react-player';
const OfflineVideoPlayer = () => {
    // The videos file name (assuming it's in the public/videos folder)
    const [videoUrl, setVideoUrl] = useState('');

    useEffect(() => {
        async function fetchCachedVideo() {
            const cacheName = 'my-video-cache'; // Replace with your cache name from the service worker
            const videoUrl = '/videos/video-1.mp4'; // Replace with the actual video file path

            try {
                const cache = await caches.open(cacheName);
                const response = await cache.match(videoUrl);

                if (response) {
                    // Video is available in the cache, use it directly
                    const videoBlob = await response.blob();
                    const videoObjectURL = URL.createObjectURL(videoBlob);
                    setVideoUrl(videoObjectURL);
                } else {
                    // Video is not in the cache, fetch it from the network
                    setVideoUrl(videoUrl);
                }
            } catch (error) {
                console.error('Error fetching cached video:', error);
                setVideoUrl(videoUrl); // Fallback to fetching from the network on error
            }
        }

        fetchCachedVideo();
    }, []);


    return (
        <div>
            <h2>Offline Video Player</h2>
            <ReactPlayer
                url={videoUrl}
                controls
                width="100%"
                height="auto"
            />
        </div>
    );
};

export default OfflineVideoPlayer;